import axios, { AxiosError } from 'axios'
import type { Moment } from 'moment'
import { toast } from 'react-toastify'

import { CalendarItemShape } from '../types/advent-calendar'

export const validateSecretKey = async (windowSeq: number, secretKey: string): Promise<boolean> => {
  const params = { secretKey: [secretKey] }
  try {
    const { data } = await axios.get(`/advent-windows/${windowSeq}/secret-key/validate`, { params })
    if (!data) {
      toast.error(' ')
    } else {
      toast.success(' ')
    }
    return data
  } catch (err) {
    toast.error('    ')
    throw err
  }
}

export const getAllCalendars = async (): Promise<CalendarItemShape[]> => {
  try {
    toast.loading('Heroku  ', {
      toastId: 1,
    })
    const { data } = await axios.get('/advent-windows')
    toast.dismiss(1)
    toast.success(' ')
    return data
  } catch (err) {
    toast.dismiss(1)
    toast.error('  .  ')
    throw err
  }
}

export const getCalendarByID = async (windowSeq: number): Promise<CalendarItemShape> => {
  try {
    const { data } = await axios.get('advent-windows', {
      params: windowSeq,
    })
    return data
  } catch (err) {
    throw err
  }
}

export const getCalendarBySecretKey = async (
  windowSeq: number,
  secretKey: string
): Promise<CalendarItemShape> => {
  const params = { secretKey: [secretKey] }
  try {
    const { data } = await axios.get(`advent-windows/${windowSeq}`, {
      params,
    })
    return data
  } catch (err) {
    toast.error('  ')
    throw err
  }
}

export const createCalendar = async (
  name: string,
  title: string,
  body: string,
  openDate: Moment,
  secretKey: string,
  contentUrl: string
): Promise<CalendarItemShape> => {
  const postDate = openDate.format('YYYY-MM-DD')
  try {
    toast.loading('', {
      toastId: 2,
    })
    const { data } = await axios.post('advent-windows', {
      name: name,
      title: title,
      body: body,
      openDate: postDate,
      secretKey: secretKey,
      contentUrl: contentUrl,
    })
    toast.dismiss(2)
    toast.success(' ')
    return data
  } catch (error) {
    const err = error as AxiosError
    toast.dismiss(2)
    if (err.response?.status === 500) {
      toast.error(' ')
    } else if (err.response?.status === 409) {
      toast.error('   ')
    } else {
      toast.error('     ')
    }
    throw err
  }
}

export const updateCalendarByID = async (
  windowSeq: number,
  title: string,
  body: string,
  secretKey: string,
  openDate: Moment,
  contentUrl: string
) => {
  const updateDate = openDate.format('YYYY-MM-DD')
  try {
    toast.loading(' ', {
      toastId: 3,
    })
    await axios.patch(`advent-windows/${windowSeq}`, {
      windowSeq: windowSeq,
      title: title,
      body: body,
      secretKey: secretKey,
      openDate: updateDate,
      contentUrl: contentUrl,
    })
    toast.dismiss(3)
    toast.success('  ')
  } catch (err) {
    toast.dismiss(3)
    toast.error(' ')
    throw err
  }
}

export const deleteCalendarByID = async (windowSeq: number, secretKey: string) => {
  try {
    toast.loading('', {
      toastId: 4,
    })
    await axios.delete(`advent-windows/${windowSeq}`, {
      data: {
        windowSeq: windowSeq,
        secretKey: secretKey,
      },
    })
    toast.dismiss(4)
    toast.success(' ')
  } catch (err) {
    toast.dismiss(4)
    toast.error(' ')
    throw err
  }
}
